import $lyfw from '@/common/LYFW/LyfwFmq.js' //旅游服务
export default {
	data() {
		return {
			piclist: [{
				ticketImage: '',
			}], //图片内容
			scSpotContent: [{
				ticketId: '',
				ticketTitle: '',
				ticketOpenUp: '',
				ticketScenicContent: '',
			}], //景区内容
			admissionTicket: [{
				admissionTicketID: '',
				admissionTicketName: '',
				ticketContain: '',
				ticketComment_s1: '',
				ticketComment_s2: '',
				ticketComment_s3: '',
				ticketAdultPrice: 240,
				ticketChildPrice: '', 
				ticketAdultPrice: '',
				companyId : '',
				executeScheduleId : '',
			}], //门票内容
			admissionTicketStatus:'',//判断是否展示
		}
	},
	onLoad(options) {
		this.lyfwData(JSON.parse(options.ticketId));
	},
	onNavigationBarButtonTap: function() {
		this.share();
	},
	onShareAppMessage(res) {
		if (res.from === 'menu') { // 来自页面内分享按钮
			return {
				title: '来自' + this.userInfo.nickname + '的分享',
				imageUrl: this.piclist[0].ticketImage,
				success: function() {
					uni.showToast({
						title: '分享成功',
						duration: 3000
					})
				},
				fail: function() {
					uni.showToast({
						title: '分享失败',
						duration: 3000
					})
				}
			}
		}
	},
	methods: {
		//读取静态数据json.js 
		lyfwData: function(e) {
			// 请求景区图片
			uni.request({
				url:$lyfw.Interface.spt_GetticketDetailByticketId.value,
				method:$lyfw.Interface.spt_GetticketDetailByticketId.method,
				data:{
					ticketId : e,
				},
				header: {'content-type': 'application/json'},
				success: (res) => {
					console.log(res)
					this.piclist = res.data.data;
					this.scSpotContent = res.data.data;
					this.admissionTicket = res.data.data;
					this.admissionTicketStatus = res.data.msg;

				}
			})

		},

		//保存图片至本地并打开新页面
		goImgList() {
			uni.setStorageSync('imagePiclist', this.piclist);
			uni.navigateTo({
				url: '/pages/LYFW/currency/imglist'
			})
		},
		//路由整合
		godetail: function(e) {
			uni.setStorage({
				key:'ticketInformation',
				data:e,
				success() {
					uni.navigateTo({
						url: '/pages/LYFW/scenicSpotTickets/orderAdd'
					})
				}
			})
			
		},

		//分享
		share() {
			uni.share({
				provider: "weixin",
				scene: "WXSceneSession",
				type: 0,
				href: "http://www.baidu.com",
				title: "来自" + this.userInfo.nickname + "的分享",
				summary: this.scSpotContent[0].ticketTitle,
				imageUrl: this.piclist[0].ticketImage,
				success: function() {
					uni.showToast({
						title: '分享成功',
						duration: 3000
					})
				},
				fail: function() {
					uni.showToast({
						title: '分享失败',
						duration: 3000
					})
				}
			});
		},

		// 统一跳转接口
		navTo(url) {
			uni.navigateTo({
				url
			})
		},

	}
}
